 import lotusAddress from "@/components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue";
 import pop from '@/components/promptOptions/pop'
 import Prompt from '@/components/zz-prompt/index.vue'
 export default{
	 data(){
		 const currentDate = this.getDate({
		             format: true
		         })
		 return{
			 promptVisible:false,promptVisible2:false,
			 uid:'',
			 token:'',
			 data:'',
			 // 性别
			 sexList:[
				{name:'男'}, 
				{name:'女'},
			 ],
			 // 地区选择
			lotusAddressData:{
			                visible:false,
			                provinceName:'',
			                cityName:'',
			                townName:'',
			            },
			            region:''
		}
	 },
	 components:{
		 lotusAddress,
		 pop,
		 Prompt
	 },
	   computed: {
	         startDate() {
	             return this.getDate('start');
	         },
	         endDate() {
	             return this.getDate('end');
	         }
	     },
	 mounted(){
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getPersonalInfo()
		} 
	 },
	 methods:{
		 // 更换头像
		 changeHeadface(){
			var  that = this;
			    uni.chooseImage({
			     count: 1,
			     sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			     success: function (res) {
					 that.$loading()
			      const tempFilePaths = res.tempFilePaths[0];
				  uni.compressImage({
				    src: tempFilePaths,
				    quality: 80,
				    success: res2 => {
					  const uploadTask = uni.uploadFile({
					   url : 'http://192.168.1.157/yishuban/app/index.php?i=2&c=entry&m=zhonghong_zhihui&do=mobile&r=api.common.upload.img',
					   filePath: res2.tempFilePath,
					   name: 'file',
					   // formData: {
					   //  'user': 'test'
					   // },
					   success: function (uploadFileRes) {
						   console.log(uploadFileRes)
					  						   uni.hideLoading()
					  						   that.$msg('修改头像成功')
					  						   that.submitPersonalInfo()
					   }
					  });
				    },
					error:(reason) =>{
						console.log(reason)
					}
				  })
			    
			  
			      // uploadTask.onProgressUpdate(function (res) {
			      //  that.percent = res.progress;
			      // });
			     },
			     error : function(e){
					 console.log(e)
					 uni.hideLoading()
					 that.$msg(e)
			     }
			    });
		 },
		  //打开picker
		         openPicker() {
		             this.lotusAddressData.visible = true;
		         },
				 toAddressList(){
					uni.navigateTo({
						url:'./addressList'
					}) 
				 },
		         //回传已选的省市区的值
		         choseValue(res){
		             //res数据源包括已选省市区与省市区code
		             this.lotusAddressData.visible = res.visible;//visible为显示与关闭组件标识true显示false隐藏
		             //res.isChose = 1省市区已选 res.isChose = 0;未选
		             if(res.isChose){
		                 this.lotusAddressData.provinceName = res.province;//省
		                 this.lotusAddressData.cityName = res.city;//市
						 this.data.province = res.province;//省
						 this.data.city = res.city;//市
		                 // this.lotusAddressData.townName = res.town;//区
		                 this.region = `${res.province} ${res.city} ${res.town}`; //region为已选的省市区的值
						  this.submitPersonalInfo()
		             }
		         },
				 // 性别选择
				 choosed(m){
					 if(m.name=='男'){
						 this.data.gender='1'
					 }else{
						 this.data.gender='2'
					 }
					  this.submitPersonalInfo()
					 uni.$off('popChoosed')
				 },
				 openSex(){
					 var that=this
					 uni.$on('popChoosed',function(e){
						 that.choosed(e)
					 })
					 this.$refs.sex.show=true
				 },
				 // 邮箱和昵称
				 clickPromptConfirm(e){
					 var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
					 if(!reg.test(e)){
						 this.$msg('请输入正确的邮箱')
						 return false;
					 }
					  this.data.email=e
					   this.submitPersonalInfo()
				 },
				 clickPromptConfirm2(e){
					 this.data.nickname=e
					  this.submitPersonalInfo()
				 },
				 // 日期选择
				 bindDateChange: function(e) {
					 var data=e.detail.value.split('-')
					 this.data.birthyear=data[0]
					 this.data.birthmonth=data[1]
					 this.data.birthday=data[2]
					 this.submitPersonalInfo()
				         },
						 getDate(type) {
						             const date = new Date();
						             let year = date.getFullYear();
						             let month = date.getMonth() + 1;
						             let day = date.getDate();
						 
						             if (type === 'start') {
						                 year = year - 60;
						             } else if (type === 'end') {
						                 year = year + 2;
						             }
						             month = month > 9 ? month : '0' + month;;
						             day = day > 9 ? day : '0' + day;
						             return `${year}-${month}-${day}`;
						         },
				 getPersonalInfo(){
					 var that=this
					 var params={
					 	uid:this.uid,
					 	token:this.token,
					 }
					 var url='&r=api.member.setting'
					   this.$apiPost(url,params).then((res) =>{
					 	  that.data=res.data
						  uni.hideLoading()
					   })
				 },
				 submitPersonalInfo(){
					 this.$loading()
					 var that=this
					 var params=this.data
					 params.uid=this.uid
					 params.token=this.token
					 var url='&r=api.member.setting.edit'
					   this.$apiPost(url,params).then((res) =>{
						   that.promptVisible=false
						   that.promptVisible2=false
						   that.getPersonalInfo()
					   })
				 }
				 
	 }
 }
